import * as yup from "yup";
import get from "lodash/get";

yup.setLocale({
  mixed: {
    required: (data: { label: string }) => {
      if (!!data?.label) {
        return `${data.label} es un valor requerido`;
      }
      return "valor requerido";
    },
    notType: (data: unknown) => {
      const type = get(data, "type");
      if (type) {
        return `este campo debera ser ${type}`;
      }

      return "tipo invalido";
    },
  },
  number: {
    positive: (data: unknown) => {
      return `${get(data, "label", " este campo")} debe ser un nmero positivo`;
    },
    moreThan: ({ more }: { more: number }) =>
      `el valor debe ser mayor que ${more}`,
    min: ({ min }: { min: number }) => `el valor mnimo es ${min}`,
    max: ({ max }: { max: number }) => `el valor mximo es ${max}`,
  },
  string: {
    matches: (data: unknown) => {
      return `formato invlido para ${get(data, "label", " este campo")}`;
    },
    email: () => {
      return "formato de email invlido";
    },
    min: ({ min }: { min: number }) =>
      `el valor mnimo es de ${min} caracteres`,
    max: ({ max }: { max: number }) =>
      `el valor mximo es de ${max} caracteres`,
  },
  array: {
    min: ({ min, label }: { min: number; path: string; label: string }) => {
      return `seleccione ${min} o ms ${label}`;
    },
  },
});

export default yup;
